import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getsupplierList } from "../../actions/supplierAction";
import { SkeletonLoading } from "../../GlobalComponent/function";
import { renderField, renderFieldSelect } from "../../renderField";

class FormSaldoAwalHutang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <Field
              label="Tanggal"
              name="tanggal"
              component={renderField}
              type="date"
            />
          </div>
          <div className="col-lg-3">
            {String(this.props.supplier) !== "" ? (
              <Field
                name="kode_supplier"
                component={renderFieldSelect}
                options={this.props.supplier.map((list) => {
                  let data = {
                    value: list.kode_supplier,
                    name: list.nama_supplier,
                  };
                  return data;
                })}
                type="text"
                label="Kode Supplier"
                placeholder="Masukan Kode Supplier"
              />
            ) : (
              <SkeletonLoading label="Kode Supplier" />
            )}
          </div>
          <div className="col-lg-3">
            <Field
              label="Total Hutang (Gr)"
              name="hutang_gr"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Total Hutang (Rp)"
              name="hutang_rp"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Total Titip (Gr)"
              name="titip_gr"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Total Titip (Rp)"
              name="titip_rp"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-12">
            <div className="text-right">
              <button
                type="submit"
                className="btn btn-info"
                disabled={this.props.onSend}
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                Selesai
                <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormSaldoAwalHutang = reduxForm({
  form: "FormSaldoAwalHutang",
  enableReinitialize: true,
})(FormSaldoAwalHutang);
export default connect((state) => {
  return {
    supplier: state.provinsi.supplier,
    onSend: state.provinsi.onSend,
  };
})(FormSaldoAwalHutang);
